      margin-left: -0.6rem; }

/* 展示 */
.vote_num_an {
  animation: vote_num 1s ease infinite; }

@keyframes vote_num {
  0% {
    transform: translateY(0px) rotate(0deg); }
  25% {
    transform: translateY(-20px) rotate(180deg); }
  50% {
    transform: translateY(-20px) rotate(360deg); }
  85% {
    transform: translateY(0px) rotate(360deg); }
  100% {
    transform: translateY(0px) rotate(360deg); } }
